.alignleft {
  /*rtl:ignore*/
  float: left;

  /*rtl:ignore*/
  margin-right: 1.5em;
  margin-bottom: 1.5em;
}

.alignright {
  /*rtl:ignore*/
  float: right;

  /*rtl:ignore*/
  margin-left: 1.5em;
  margin-bottom: 1.5em;
}

.aligncenter {
  clear: both;

  @include center-block;

  margin-bottom: 1.5em;
}
.text-md-left {
  @include media-breakpoint-up(md) {
    text-align: left !important;
  }
}
.text-md-right {
  @include media-breakpoint-up(md) {
    text-align: right !important;
  }
}
