.section-content-five {
  ul,
  ol {
    margin: 0;
  }
  ul {
    list-style-image: url("data:image/svg+xml,%3Csvg width='20' height='19' viewBox='0 0 20 19' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M20 7.24L12.81 6.62L10 0L7.19 6.63L0 7.24L5.46 11.97L3.82 19L10 15.27L16.18 19L14.55 11.97L20 7.24ZM10 13.4L6.24 15.67L7.24 11.39L3.92 8.51L8.3 8.13L10 4.1L11.71 8.14L16.09 8.52L12.77 11.4L13.77 15.68L10 13.4Z' fill='%232196F3'/%3E%3C/svg%3E%0A");
    li {
      padding-left: rem-calc(10);
    }
  }

  @include media-breakpoint-down(md) {
    padding: 0;
  }

  .section-col-img {
    @include media-breakpoint-down(md) {
      padding: 0;
    }
  }
  &.img-right {
    .section-heading,
    .section-col-img {
      display: flex;
      justify-content: end;
    }
    .section-col-img {
      @include media-breakpoint-down(md) {
        display: block;
      }
    }
  }
  .section-heading {
    padding-left: rem-calc(52);
    @include media-breakpoint-down(md) {
      padding: rem-calc(18 24 74);
    }
    &__title {
      max-width: rem-calc(377);
      @include media-breakpoint-down(md) {
        font-weight: 500;
        font-size: rem-calc(32);
        line-height: rem-calc(40);
      }
    }
    &__desc {
      max-width: rem-calc(325);
    }
  }

  img {
    height: rem-calc(850);
    @include media-breakpoint-down(md) {
      width: 100%;
      height: rem-calc(380);
    }
  }

  .section-heading__btn {
    margin-top: rem-calc(24);
  }
}
